import React, {useState, useEffect} from 'react';

const DATA_URL = 'https://api.covid19india.org/website_data.json';
function World(props) {
    const [faq, setFaq] = useState([]);
  
    useEffect(() => {
      getFAQs();
    }, []);
  
    useEffect(() => {
      window.scrollTo(0, 0);
    }, []);
  
    const getFAQs = () => {
      fetch(DATA_URL)
        .then((response) => {
          return response.json();
        })
        .then((data) => {
          setFaq(data.faq);
        })
        .catch((error) => {
          console.log(error);
        });
    };
return (
    <div id="unique2">
      <iframe height="100%" width="100%" src="https://covid19world.sofaio.tech/"> </iframe>
    </div>
    );
}
export default World;